
.container {
    padding: 15px;
    padding-left: 50px;
    display: flex;
   border-radius: 10px;
    justify-content: center;
    align-items: flex-start;
    /* backdrop-filter: blur(5px); */
  }

.title{
    padding: 15px;
    color: rgb(18, 30, 63);
    height: 15px;
    margin: 0;
    margin-left: 100px;
    /* backdrop-filter: blur(30px); */
    border-radius: 15px  ;
}
.list_wrap{
    padding: 20px;
}
.list{
   padding: 0;
   margin: 0;
    display: flex;
    padding: 10px;
    border-radius: 15px;
    
    color: white;
    flex-direction: column;
    align-items: center;
    
    backdrop-filter: blur(60px);
    border-bottom: 0.5px solid wheat;
    border-top: 0.5px solid wheat;
}
.item{
    border-bottom: 0.5px solid rgb(245, 239, 230);
    border-top: 0.5px solid rgb(246, 241, 232);
    cursor: pointer;
    background-color: rgb(18, 30, 63);
    /* background: url('hhttps://img.freepik.com/free-photo/vivid-blurred-colorful-background_58702-2655.jpg'); */
    margin-top: 7px;
    display: flex;
    border-radius: 15px  ;
    color: wheat;
   
    width: 300px;
    height: 35px;
    align-items: center;
    justify-content: center;
}
.text{
    margin: 0;
}